import { FC } from 'react';

import { ButtonProps } from './Button.model';

// –≤–º–µ—Å—Ç–æ —Ç–æ–≥–æ —á—Ç–æ –±—ã —Å—Ç—Ä–∞–Ω–Ω–æ –≤—ã–±–∏—Ä–∞—Ç—å –∏–∫–æ–Ω–∫–∏ –Ω–∞–¥–æ —Å–¥–µ–ª–∞—Ç—å –∫–æ–º–ø–æ–Ω–µ–Ω—Ç—É –∫–Ω–æ–ø–∫–∏,
// –∫–æ—Ç–æ—Ä–∞—è –º–æ–≥–ª–∞ –±—ã –æ–±–æ—Ä–∞—á–∏–≤–∞—Ç—å —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ –∫–Ω–æ–ø–∫–∏ –∏–ª–∏ –µ—â–µ –∫–∞–∫ –Ω–∏–±—É–¥—å (HOC)
const Button: FC<ButtonProps> = ({ onBtnClick, text, className, icon }) => {
  const handleClick = () => {
    onBtnClick();
  };

  const leftIcon = icon === 'arrow-left' && (
    <span className="icon is-small">
      <i className="fas fa-arrow-left" />
    </span>
  );

  const rightIcon = icon === 'arrow-right' && (
    <span className="icon is-small">
      <i className="fas fa-arrow-right" />
    </span>
  );

  return (
    <button onClick={handleClick} className={`button ${className}`}>
      {leftIcon}
      <span>{text}</span>
      {rightIcon}
    </button>
  );
};

export default Button;
